	module.exports = {"data":{"site":{"siteMetadata":{"title":"Glen Cheney"}},"markdownRemark":{"html":"<p>Categorize, sort, and filter a responsive grid of items.</p>\n<h2>Hello world</h2>\n<p>Shuffle started as a side project while I was still at <a href=\"http://www.rit.edu\">RIT</a> in December 2011. It used to be a jQuery plugin with a dependency on Modernizr. <a href=\"https://isotope.metafizzy.co/\">Isotope</a>, a great layout plugin, inspired me to create my own with a focus on filtering and sorting items.</p>\n<h2>Iteration</h2>\n<p>Shuffle has evolved over the years. I rewrote it multiple times, removed its dependency on jQuery and Modernizr, added unit tests, added Rollup to generate bundles, and added TypeScript definitions.</p>\n<p>Along the way, it has also been used in multiple Odopod projects.</p>\n<p>Shuffle and its docs site has been a great outlet for me to try new web features and build tools, like Rollup and Gulp, before using them in client projects.</p>\n<!-- markdownlint-disable MD033 -->\n<video muted playsinline controls loop poster=\"/shuffle-poster.png\">\n  <source src=\"/static/shuffle-d64f7d5c6ce177c0f5603abe32669d27.webm\" type=\"video/webm; codecs=vp9,vorbis\">\n  <source src=\"/static/shuffle-d51b9f6f0e2487ddf49a7784c9f3cc35.mp4\" type=\"video/mp4\">\n</video>\n<h2>Community</h2>\n<p>GitHub users have opened more than 150 issues with a wide variety of questions, feature requests, and bugs.</p>\n<h2>Current status</h2>\n<p>I actively maintain the package and occasionally contribute new features, documentation, and demos. npm users download the <a href=\"https://www.npmjs.com/package/shufflejs\"><code class=\"language-text\">shufflejs</code></a> package thousands of times per month.</p>","frontmatter":{"title":"Shuffle","imageDescription":"Screenshot of the Shuffle package demo site.","href":"https://vestride.github.io/Shuffle/","tags":["open source","component","build systems"]}},"hero":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.777434312210201,"src":"/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-0fe8f.png","srcSet":"/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-d7863.png 350w,\n/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-c146b.png 700w,\n/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-0fe8f.png 1400w,\n/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-25199.png 2100w,\n/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-9880c.png 2300w","srcWebp":"/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-43730.webp","srcSetWebp":"/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-1130a.webp 350w,\n/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-7944d.webp 700w,\n/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-43730.webp 1400w,\n/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-beeb1.webp 2100w,\n/static/shuffle-hero-4e81f9d52a287a773187a47bd388ccb7-71e2f.webp 2300w","sizes":"(max-width: 1400px) 100vw, 1400px"}}},"pathContext":{"slug":"/shuffle/","heroImage":"/images/shuffle-hero.png/","nextProject":{"frontmatter":{"title":"we.odopod"},"fields":{"slug":"/we-dot-odopod/"}}}}

